.thumb {
  width: 210px;
  position: relative;
  margin-bottom: 10px;
  cursor: pointer;

  img {
    display: block;
    width: 100%;
  }
}

.thumb-large img {
  display: block;
  width: 100%;
}

.thumb .state-icon {
  display: block;
  width: 14px;
  height: 14px;
  position: absolute;
  bottom: 0;
  right: 0;
  background: url(../img/enlarge.png) no-repeat;
}

.thumb-large {
  .state-icon {
    display: block;
    width: 14px;
    height: 14px;
    position: absolute;
    bottom: 0;
    right: 0;
    background: url(../img/enlarge.png) no-repeat;
    background: url(../img/shrink.png) no-repeat;
    z-index: 111;
  }

  .caption {
    width: 100%;
    color: rgb(255, 255, 255);
    background-color: rgb(0, 0, 0);
    background-color: rgba(0, 0, 0, 0.5);
    position: absolute;
    bottom: 0;
    right: 0;

    p {
      margin: 0;
      padding: 10px;
    }
  }

  position: absolute;
  top: 0;
  left: 0;
  z-index: 110;
}
