.instagram-head {
  width: 92%;
  height: 40px;
  background-color: #4B779F;
  font-size: 18px;
  border-radius: 5px 5px 0 0;
  text-align: left;
  margin-left: 5px;

  img {
    width: 35px;
    height: 35px;
    display: inline-block;
    vertical-align: top;
    margin-top: 3px;
    margin-left: 10px;
  }

  p {
    display: inline-block;
    color: #fff;
    margin: 9px 0 0 14px;
    font-size: 19px;
  }
}

.instagram-we {
  width: 92%;
  height: 90px;
  text-align: left;
  margin-bottom: 5px;
  margin-left: 5px;

  img {
    width: 90px;
    display: inline-block;
    vertical-align: top;
  }
}

.instagram-we-right {
  display: inline-block;
  margin: 22px 0 0 28px;
}

.instagram-we-button-wrapper {
  -webkit-box-shadow: 1px 1px 10px 0 rgba(97, 96, 97, 1);
  -moz-box-shadow: 1px 1px 10px 0 rgba(97, 96, 97, 1);
  box-shadow: 1px 1px 10px 0 rgba(97, 96, 97, 1);
  border-radius: 5px;
}

.instagram-we-button {
  background-color: #AD4141;
  color: #fff;
  margin: 5px;
  display: inline-block;
  border-radius: 3px;
  padding: 5px 25px;

  p {
    display: inline-block;
  }

  span {
    width: 0;
    height: 0;
    display: inline-block;
    border-left: 10px solid #fff;
    border-top: 5px solid transparent;
    border-bottom: 5px solid transparent;
    margin-left: 5px;
  }
}

.options {
  width: 28px;
  position: absolute;
  left: 2px;
  background: #615F5F;
  padding: 3px;
  display: none;

  li {
    margin: 0 !important;
    padding: 0 !important;
    border: 0 !important;

    img {
      width: 100%;
    }
  }
}

#thanks {
  width: 460px !important;
  max-width: 460px !important;
  margin: 0 auto !important;
  position: fixed !important;
  border-radius: 0;
  padding: 0;

  a.close-jmodal {
    background: transparent url("../img/close-button.png") no-repeat scroll 0 0;
    right: 5px;
    top: 5px;
  }
}

.thanks-wrapper {
  p {
    text-align: center;
    font-size: 23px;
    margin: 20px 10px 10px 10px;
    color: #39752A;
  }

  a {
    background-color: #c7db1e;
    border-radius: 4px;
    color: #fff;
    display: block;
    font-family: DendaNewLightC;
    font-size: 23px;
    margin-left: 4px;
    padding: 2px 1px;
    text-align: center;
    text-decoration: none;
    width: 130px;
    margin: 0 auto;
    margin-bottom: 15px;
  }
}

#contacts .social-icons {
