  .header {
    width: 100%;
    background: url('../img/header-background.jpg') no-repeat transparent;
    background-size: 100% 601px;
    background-position: center top;
    display: inline-block;
    text-align: center;

    .menu {
      background-color: rgba(255, 255, 255, 0.78);
      display: inline-block;
      font-family: DendaNewC;
      height: 48px;
      margin-top: 14px;
      text-align: center;
      width: 100%;

      > ul > li > a > img {
        width: 25px;
        margin-top: 2px;
        vertical-align: top;
      }

      ul li:first-child {
        height: 24px;
        padding-top: 3px;
        vertical-align: baseline;
      }
    }

    &.fixed {
      padding-top: 62px;

      .menu {
        display: block;
        position: fixed;
        top: 0;
        margin-top: 0;
        z-index: 1000;
        -webkit-box-shadow: 0 2px 3px 0 rgba(97, 96, 97, 0.64);
        -moz-box-shadow: 0 2px 3px 0 rgba(97, 96, 97, 0.64);
        box-shadow: 0 2px 3px 0 rgba(97, 96, 97, 0.64);
        background-color: rgba(255, 255, 255, 1);
      }
    }

    .menu ul {
      display: inline-block;
      list-style: none;

      li {
        border-right: 3px solid #555;
        display: inline-block;
        margin-top: 11px;
        padding: 0 7px;

        &:last-child {
          border-right: none;
        }

        a {
          color: #2e2e2e;
          text-decoration: none;

          &:hover {
            color: #A5A2A2;
          }
        }
      }
    }
  }

  @media screen and (min-width: 1390px) {
    .header .menu ul li {
      font-size: 21.1px;
    }
  }

  @media screen and (min-width: 1170px) and (max-width: 1389px) {
    .header .menu ul li {
      font-size: 18px;
    }
  }

  @media screen and (min-width: 1085px) and (max-width: 1169px) {
    .header .menu ul li {
      font-size: 18px;
    }
  }

  @media screen and (max-width: 1084px) {
